document.addEventListener("DOMContentLoaded", function () {
    const return1 = document.getElementById("return1");
    return1.addEventListener("click", function(e) {
        e.preventDefault();
        window.location.href = "/form.php";
    })
});

function alcoholEnable() {
    const enableMsg = document.getElementById("form-alcohol-select").value;
    const field = document.getElementById("form-alcohol-field");
    if (enableMsg == "yes") {
        field.classList.remove("display-none");
    } else {
        field.classList.add("display-none");
    }
}

